<template>
    <div class=" container text-center product-category">
      <h2>Sản Phẩm</h2>
      <div class="row">
        <div class="col-3 product" v-for="product in filteredProducts" :key="product.id">
          <div class="card products" >
            <div class="card-header">
              <h3>{{ product.name }}</h3>
            </div>
            <div class="card-body"><img :src="require(`@/assets/img/${product.img}`)"></div>
            <div class="card-footer">
              <h4>{{ formatPrice(product.price) }}</h4>
              <button class="btn btn-success">
                <router-link class="nav-link" :to="'/product/' + product.id">Xem chi tiết</router-link></button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'ProductCategory',
    props: {
      category: {
        type: String,
        required: true
      }
    },
    data() {
      return {
        products: []
      };
    },
    created() {
      this.fetchProducts();
    },
    methods: {
        formatPrice(price) {
      return price.toLocaleString('vi-VN', { style: 'currency', currency: 'VND' });
    },
      async fetchProducts() {
        try {
          const response = await fetch('http://localhost:3000/products'); // Adjust URL as needed
          if (!response.ok) {
            throw new Error('Failed to fetch products');
          }
          this.products = await response.json();
        } catch (error) {
          console.error('Error fetching products:', error);
        }
      }
    },
    computed: {
      filteredProducts() {
        return this.products.filter(product => product.category === this.category);
      }
    }
  }
  </script>
  
  <style scoped>
  .product-category {
    margin-top: 20px;
  }
  
  .products {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
  </style>
  