<template>
    <div class="container form">
        <div class="details">
            <h2>Đăng nhập</h2>
            <form @submit.prevent="submit">
                <div class="input-container">
                    <label>Email</label>
                    <div>
                        <input type="email" placeholder="Email" v-model="email" autocomplete="username">
                    </div>
                </div>
                <div class="input-container">
                    <label>Mật Khẩu</label>
                    <div>
                        <input type="password" placeholder="Password" v-model="password" autocomplete="current-password">
                    </div>
                </div>
                <div class="button">
                    <button type="submit">Đăng nhập</button>
                </div>
                <div v-if="successMessage" class="success-message">
                    {{ successMessage }}
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { ref } from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

export default {
    name: "LoginView",

    setup() {
        const email = ref('');
        const password = ref('');
        const successMessage = ref('');
        const errorMessage = ref('');
        const router = useRouter();
        const store = useStore();

        const submit = async () => {
            try {
                const response = await axios.get('http://localhost:3000/Users');
                const users = response.data;
                const user = users.find(u => u.email === email.value && u.password === password.value);

                if (user) {
                    successMessage.value = "Chúc Mừng Bạn Đăng Nhập Thành Công!";
                    store.commit('setLoggedIn', true);
                    store.commit('setUser', user);
                    setTimeout(() => {
                        if (user.admin) {
                            router.push('/dashboard');
                        } else {
                            router.push('/');
                        }
                    }, 2000);
                } else {
                    errorMessage.value = 'Invalid email or password';
                }
            } catch (error) {
                console.error('Error:', error);
                errorMessage.value = 'An error occurred during login. Please try again later.';
            }
        };

        return {
            email,
            password,
            submit,
            successMessage
        };
    }
};
</script>

<style>
.form{
    margin-bottom: 100px;
    margin-top: 50px;
    width: 500px;
}
</style>
