<template>
    <div class="container form">
        <div class="details">
            <h2>Đăng kí</h2>
            <form @submit.prevent="submit">
                <div class="input-container">
                    <label>Tên người dùng</label>
                    <div>
                        <input type="text" placeholder="Name" v-model="name" autocomplete="username">
                    </div>
                </div>

                <div class="input-container">
                    <label>Email</label>
                    <div>
                        <input type="email" placeholder="Email" v-model="email">
                    </div>
                </div>

                <div class="input-container">
                    <label>Số điện thoại</label>
                    <div>
                        <input type="text" placeholder="Phone" v-model="phone" autocomplete="tel">
                    </div>
                </div>

                <div class="input-container">
                    <label>Mật Khẩu</label>
                    <div>
                        <input type="password" placeholder="Password" v-model="password" autocomplete="current-password">
                    </div>
                </div>

                <div class="input-container">
                    <label>Chức vụ</label>
                    <div>
                        <input type="radio" id="admin" value="admin" v-model="accountType"> Nhân Viên |
                        <input type="radio" id="user" value="user" v-model="accountType"> Khách Hàng
                    </div>
                </div>

                <div class="button">
                    <button type="submit">Đăng kí</button>
                </div>

                <div v-if="successMessage" class="success-message">
                    {{ successMessage }}
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { ref } from 'vue';
import { useRouter } from 'vue-router';

export default {
    name: "RegisterView",
    setup() {
        const name = ref('');
        const email = ref('');
        const phone = ref('');
        const password = ref('');
        const accountType = ref('user'); // Default account type is 'user'
        const successMessage = ref('');
        const router = useRouter();

        const submit = async () => {
            try {
                // Send a POST request to the JSON database
                const response = await axios.post('http://localhost:3000/Users', {
                    name: name.value,
                    email: email.value,
                    phone: phone.value,
                    password: password.value,
                    admin: accountType.value === 'admin' // Set 'admin' field based on selected account type
                });

                // Handle the response from the database
                console.log('Response:', response.data);

                // Display success message
                successMessage.value = 'Registration successful! Redirecting...';

                // Redirect to appropriate page after 2 seconds
                setTimeout(() => {
                        router.push('/login');
                }, 2000);
            } catch (error) {
                console.error('Error:', error);
            }
        };

        return {
            name,
            email,
            phone,
            password,
            accountType,
            submit,
            successMessage
        };
    }
};
</script>
