<template>
  <div class="container text-center">
    <h3 class="pb-3 pt-3">Sản Phẩm</h3>
    <div class="row">
      <div class="col-3" v-for="product in products" :key="product.name">
        <div class="card products" >
          <div class="card-header">
            <h3>{{ product.name }}</h3>
          </div>
          <div class="card-body"><img :src="require(`../assets/img/${product.img}`)"></div>
          <div class="card-footer">
            <h4>{{ formatPrice(product.price) }}</h4>
            <button class="btn btn-success">
              <router-link class="nav-link" :to="'/product/' + product.id">Xem chi tiết</router-link></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'ProductsView',
  data() {
    return {
      products: []
    }
  },
  methods: {
    formatPrice(price) {
      return price.toLocaleString('vi-VN', { style: 'currency', currency: 'VND' });
    },
    async fetchProducts() {
      try {
        const response = await axios.get('http://localhost:3000/products');
        this.products = response.data;
      } catch (error) {
        console.error('Error fetching products:', error);
      }
    }
  },
  mounted() {
    this.fetchProducts();
  }
}
</script>

<style>
.card {
  width: 250px;
  text-align: center;
  margin-bottom: 20px;
}

.card-body {
  height: 250px;
}

.card img {
  width: 90%;
  text-align: center;
  align-items: center;
}
</style>
