<template>
  <div class="product-detail container" v-if="product">
    <div class="row">
      <div class="col-lg-4 col-md-6">
        <img :src="require(`../assets/img/${product.img}`)" alt="Product Image" class="product-image">
      </div>
      <div class="col-lg-8 col-md-6">
        <form @submit.prevent="handleAddToCart" class="product-form">
          <h1>Tên Sản Phẩm: {{product.name}}</h1>
          <div class="price">Giá:{{formatPrice(product.price)}}</div>
          <div class="form-group">
            <label for="productQuantity">Số Lượng: </label>
            <div class="quantity-controls">
              <input type="number" name="quantity" id="quantity" v-model="quantity" min="1" :max="product.inStock" class="quantity-input">
            </div>
          </div>
          <button type="submit" class="btn btn-primary add-to-cart-btn">Thêm vào giỏ hàng</button>
        </form>
      </div>
    </div>
  </div>
  <div v-else>
    <p>Sản phẩm không tồn tại</p>
  </div>
</template>
<script>
import axios from 'axios';

export default {
  name: 'ProductDetailView',
  props: ['id'],
  data() {
    return {
      product: null,
      quantity: 1
    };
  },
  created() {
    this.loadProduct(this.id);
  },
  methods: {
    formatPrice(price) {
      return price.toLocaleString('vi-VN', { style: 'currency', currency: 'VND' });
    },
    async loadProduct(id) {
      try {
        const response = await axios.get(`http://localhost:3000/products/${id}`);
        this.product = response.data;
      } catch (error) {
        console.error('Error fetching product:', error);
      }
    },
    handleAddToCart() {
      const cartItem = {
        img:this.product.img,
        id: this.product.id,
        name: this.product.name,
        price: this.product.price,
        quantity: this.quantity,
      };
      this.$store.dispatch('addToCart', cartItem);
      this.$router.push('/cart');
    },
  }
};
</script>

<style scoped>
.product-detail {
  margin-top: 100px;
  margin-bottom: 100px;
}

.product-image {
  max-width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.product-form {
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

h1 {
  color: #333;
  font-size: 28px;
  margin-bottom: 10px;
}

.price {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 15px;
}

.form-group {
  margin-bottom: 20px;
}

label {
  font-weight: bold;
  font-size: 18px;
  color: #333;
}

.quantity-input {
  width: 100px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
}

.add-to-cart-btn {
  padding: 12px 24px;
  font-size: 18px;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-to-cart-btn:hover {
  background-color: #0056b3;
}
</style>