<template>
  <div>
      <nav class="navbar navbar-expand-lg bg-body-tertiary">
        <div class="container-fluid">
          <div class="collapse navbar-collapse" id="navbarNavDropdown">
            <ul class="navbar-nav">
              <li class="nav-item">
                <router-link class="nav-link" to="/">Trang chủ</router-link>
              </li>
              <li class="nav-item dropdown">
                <a class="nav-link dropdown-toggle" href="#" id="navbarDropdownMenuLink" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                  Sản Phẩm
                </a>
                <ul class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                  <router-link class="nav-link" :to="{ name: 'ProductCategory', params: { category: 'Fruit'} }">Trái cây</router-link>
                  <router-link class="nav-link" :to="{ name: 'ProductCategory', params: { category: 'Vegetable'} }">Rau Củ</router-link>
                </ul>
              </li>
              
            </ul>
            <ul class="navbar-nav ml-auto nav-right">
              <li v-if="isLoggedIn" class="nav-item">
                <a class="nav-link" @click="logout">Đăng xuất</a>
              </li>
              <li v-if="isLoggedIn" class="nav-item">
                <p class="nav-link text-danger">{{ username }}</p>
              </li>
              <!-- Di chuyển phần giỏ hàng và các nút đăng nhập, đăng ký sang bên phải -->
              <li v-if="!isLoggedIn" class="nav-item">
                <router-link class="nav-link" to="/login">Đăng nhập</router-link>
              </li>
              <li v-if="!isLoggedIn" class="nav-item">
                <router-link class="nav-link" to="/register">Đăng ký</router-link>
              </li>
              <li v-if="!isLoggedIn" class="nav-item cart-icon">
                <router-link class="nav-link" to="/cart">
                  <i class="fas fa-shopping-cart"></i>
                </router-link>
              </li>
            </ul>
          </div>
        </div>
      </nav>      
  </div>
</template>

<script>
export default {
  name: 'HeaderComponent',
  computed: {
    isLoggedIn() {
      return this.$store.state.isLoggedIn;
    },
    username() {
      return this.$store.state.user ? this.$store.state.user.name : '';
    }
  },
  methods: {
    logout() {
      this.$store.commit('setLoggedIn', false);
      this.$router.push('/login')
    }
  }
}
</script>

<style>
/* Hiệu ứng lơ lửng */
.nav-item:hover {
  background-color: #f8f9fa; /* Màu nền khi di chuột qua */
}

.cart-icon i {
  font-size: 1.2rem; /* Kích thước biểu tượng giỏ hàng */
}
.nav-right{
  position: absolute;
  right: 20px;
  top: 50%;
  
}
/* Đặt giỏ hàng bên góc phải */
.cart-icon {
  position: absolute; 
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.3s ease; /* Thêm hiệu ứng chuyển động */
}

/* Hiệu ứng khi rê chuột qua biểu tượng giỏ hàng */
.cart-icon:hover {
  transform: translateY(-50%) scale(1.1); /* Phóng to khi rê chuột qua */
}

</style>
